<template>
  <div class="orders">
    <breaks first="订单管理" scenter="订单列表"></breaks>
    <el-card class="box-card">
      <div class="inpnt-type">
        <el-input placeholder="请输入内容" class="input-with-select" v-model="params.query">
          <el-button slot="append" icon="el-icon-search" @click="getOrders"></el-button>
        </el-input>
      </div>
      <el-table
        :data="tableData"
        border
        style="width: 100%">
        <el-table-column
          label="#"
          type="index"
          width="50">
        </el-table-column>
        <el-table-column label="订单编号" width="235" prop="order_number"></el-table-column>
        <el-table-column label="订单价格" width="230" prop="order_price"></el-table-column>
        <el-table-column label="是否付款" width="230" prop="pay_status">
          <template slot-scope="scoped">
            <el-tag type="danger" v-if="scoped.row.pay_status==='0'">未付款</el-tag>
            <el-tag v-else>已付款</el-tag>
          </template>
        </el-table-column>
        <el-table-column label="是否发货" width="230" prop="is_send"></el-table-column>
        <el-table-column label="下单时间" width="230" prop="create_time">
          <template slot-scope="scoped">
            {{ scoped.row.create_time | msgDate }}
          </template>
        </el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scoped">
            <!-- 修改按钮 -->
            <el-button type="primary" icon="el-icon-edit" size="mini"></el-button>
            <!-- 物流信息按钮 -->
            <el-button type="success" icon="el-icon-location-outline" size="mini"
                       @click="abeselin(scoped.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :page-sizes="[5, 10, 20, 50]"
        :page-size="params.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
      <el-dialog
        title="物流进度"
        :visible.sync="dialogVisible"
        width="30%">
        <div class="block">
          <el-timeline>
            <el-timeline-item
              v-for="(activity, index) in activities"
              :key="index"
              :icon="activity.icon"
              :type="activity.type"
              :color="activity.color"
              :size="activity.size"
              :timestamp="activity.time">
              {{ activity.context }}
            </el-timeline-item>
          </el-timeline>
        </div>
        <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
  </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  name: '',
  data () {
    return {
      dialogVisible: false,
      tableData: [],
      formRout: {},
      total: 0,
      params: {
        query: '',
        pagenum: 1,
        pagesize: 5
      },
      activities: [],
      dialogFormVisible: false,
      updateFormVisible: false,
      rid: '',
      formLabelWidth: '80px'
    }
  },
  created () {
    this.getlist()
  },
  methods: {
    async getlist () {
      const res = await this.$http('/orders', { params: this.params })
      // console.log(res.data.data)
      this.tableData = res.data.data.goods
      this.total = res.data.data.total
    },
    getOrders () {
    },
    handleSizeChange (val) {
      this.params.pagesize = val
      this.getlist()
    },
    handleCurrentChange (val) {
      this.params.pagenum = val
      this.getlist()
    },
    async abeselin (rows) {
      this.dialogVisible = true
      const res = await this.$http(`/kuaidi/${rows.order_id}`)
      this.activities = res.data.data
      this.activities[0].icon = 'el-icon-location-outline'
      this.activities[0].color = '#0bbd87'
      this.activities[0].size = 'large'
    }
  },
  computed: {}
}
</script>

<style lang="less" scoped>
.orders {
  width: 100%;
  height: 100%;

  .el-breadcrumb {
    font-size: 12px;
  }

  .text {
    font-size: 14px;
  }

  .item {
    padding: 18px 0;
  }

  .box-card {
    width: 100%;
    margin-top: 15px;
  }

  .inpnt-type {
    .input-with-select {
      width: 30%;
      margin-right: 20px;
    }
  }

  .el-table {
    margin: 15px 0;
  }
}

</style>
